header {
  grid-area: 1 / 1;
}

.forum {
  height: 100vh;
  flex-direction: column;
  grid-row: 2 / 4;
  align-items: center;
  display: flex;
}

.forum-wrapper {
  height: auto;
  background: linear-gradient(#141e30, #243b55);
  grid-template: auto auto auto / auto;
  display: grid;
}

main {
  grid-area: 2 / 1;
}

main .category-wrapper {
  box-sizing: border-box;
  color: #fff;
  height: 100%;
  background: #00000080;
  border-radius: 10px;
  flex-direction: column;
  gap: 10px;
  margin-top: 50px;
  margin-bottom: 50px;
  padding: 1rem;
  overflow-y: scroll;
  box-shadow: 0 15px 25px #000;
}

main h2 {
  color: #fff;
  margin: 1rem 0 -1rem;
}

.post-forms {
  height: 20vh;
  border: .1em dotted #f5f5f5;
  flex-direction: column;
  justify-content: center;
  display: flex;
}

main .form-container form input, #message {
  color: #fff;
  resize: none;
  background: #00000080;
  border: none;
  margin-bottom: .07em;
  padding: .5rem;
}

input[type="submit"], input[type="reset"] {
  cursor: pointer;
}

main .category-wrapper .message-wrapper {
  background: #00000080;
  border-radius: .5rem;
  grid-template: auto auto / 90% 10%;
  margin-top: .5rem;
  margin-bottom: .5rem;
  padding: .5rem;
  display: grid;
}

main .category-wrapper .message-wrapper div {
  grid-template: auto auto / auto auto;
  grid-area: 1 / 1 / auto / span 2;
  display: grid;
}

main .category-wrapper .message-wrapper div .post-id {
  display: none;
}

main .category-wrapper .message-wrapper div .post-title {
  grid-area: 1 / 1;
  font-size: 1rem;
}

main .category-wrapper .message-wrapper div .poster {
  grid-area: 2 / 1 / auto / span 2;
  margin: .5rem 0;
}

main .category-wrapper .message-wrapper div .poster a {
  color: #fff;
}

main .category-wrapper .message-wrapper div .timestamp {
  color: #353535;
  grid-area: 1 / 2;
}

main .category-wrapper .message-wrapper button {
  color: #fff;
  background: #00000080;
  border: none;
  border-radius: .5rem;
  padding: .5rem;
}

footer {
  grid-area: 3 / 1;
}

